.fourth-container{

}
.inner-Fourth-Container{
    display: flex;
    max-width: 100%;
    justify-content: flex-end;
}
.fourth-title{
    position: relative;
    min-width: 66.5%;
    margin-right: 0.5%;
}
.fourth-row-img{
    position: relative;
    display: flex;
    flex-direction: column;
min-width: 33%;
}
.fourth-img{
    width: 100%;
    height: 100%;
}
.fourth-row-effect{
    filter: grayscale(0);
    transition: 1s; 
}

.fourth-row-effect:hover{
    filter: grayscale(1);
}